import "./styles.css";
import { useState, useEffect, useRef } from "react";
import { db } from "./utils/firebaseconfig";
import Button from "./components/buttons";
import {
  collection,
  addDoc,
  getDocs,
  doc,
  deleteDoc,
  updateDoc
} from "firebase/firestore";

export default function App() {
  const [tasks, setTask] = useState([]);
  const [title, setTitle] = useState("");
  let collectionref = collection(db, "task");
  const [isChecked, setIsChecked] = useState(0);

  useEffect(() => {
    const getuser = async () => {
      const getdata = await getDocs(collectionref);
      const users = getdata.docs;
      setTask(
        users.map((doc) => {
          return { ...doc.data(), id: doc.id };
        })
      );
    };
    getuser();
    console.log("useeffect");
  }, [isChecked]);

  const changehandle = (e) => {
    const { value } = e.target;
    setTitle(value);
  };

  const addTask = async (e) => {
    e.preventDefault();
    try {
      const adduser = await addDoc(collectionref, { title });
      if (adduser) {
        console.log("Task Added Successfully");
      }
      setIsChecked((prev) => prev + 1);
    } catch (error) {
      console.log(error);
    }
  };
  const deleteTask = async (e, id) => {
    e.preventDefault();
    const docref = doc(db, "task", id);
    try {
      const deleteuser = await deleteDoc(docref);
      if (deleteuser) {
        console.log("Task deleted Successfully");
      }
      setIsChecked((prev) => prev + 1);
    } catch (error) {
      console.log(error);
    }
  };
  const updateTask = async (e, id) => {
    e.preventDefault();
    const docref = doc(db, "task", id);
    try {
      const updateTask = await updateDoc(docref, { title });
      if (updateTask) {
        console.log("Task Updated Successfully");
      }
      setIsChecked((prev) => prev + 1);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div>
        <input
          type="text"
          placeholder="Enter Task"
          onChange={changehandle}
          maxLength="20"
        />
        <button type="submit" onClick={addTask}>
          Add Task
        </button>
      </div>
      <ul>
        {tasks.map((task) => {
          return (
            <li key={task.id}>
              {task.title}
              <Button id={task.id} changeTask={deleteTask} value="delete" />
              <br />
              <input
                type="text"
                placeholder="Enter New Value"
                onChange={changehandle}
                maxLength="20"
              />
              <Button id={task.id} changeTask={updateTask} value="update" />
            </li>
          );
        })}
      </ul>
    </>
  );
}
